
<template>
  <div>
    <div class="d-flex">
        <h4 class="font-weight-bold py-3 mb-0">{{ pageTitle }}</h4>
        <div class="ml-auto">
          <CButton @click="sendNewForm" size="lg" color="warning">
            {{ saveBtnTtile }}
          </CButton>
        </div>
    </div>
    
    <CCard id="form-info" class="my-4">
      <CCardHeader>
        <h5>表單設定</h5>
      </CCardHeader>
      <CCardBody>
        <CInput 
          v-model="sheetTitle.value"
          label="試算表名稱"
          placeholder="ex. 0610"
          invalid-feedback="請填入試算表名稱"
          :is-valid="sheetTitle.validator(sheetTitle)"
          size="lg"
          :readonly="editSheet!=null"
        />
        <CInput 
          v-model="title.value"
          label="表單標題"
          placeholder="三葉家庭日,外帶表單"
          invalid-feedback="請填入表單標題"
          :is-valid="sheetTitle.validator(title)"
          size="lg"
        />
        <CTextarea
          v-model="describe.value"
          label="說明文字"
          placeholder="三葉與大家一起防疫大作戰！"
          invalid-feedback="請填入說明文字"
          :is-valid="sheetTitle.validator(describe)"
          size="lg"
          rows="5"
        />
      </CCardBody>
    </CCard>
    
    <CCard id="form-valid-time" class="my-4">
      <CCardHeader>
        <h5>訂購時間設定</h5>
      </CCardHeader>
      <CCardBody>
        <CRow>
          <CCol sm="2">
            <h5>有效時間</h5>
          </CCol>
          <CCol sm="6">
            <date-picker
              range
              type="datetime"
              format="YYYY-MM-DD HH:mm"
              valueType="format"
              :minute-step="60"
              :default-value="new Date()"
              :disabled-date="disabledBeforeTodayAndAfterAWeek"
              v-model="validTime"
            >
            </date-picker>
            <CAlert :show="validTime == null" color="danger" class="my-2">請選擇有效時間</CAlert>
          </CCol>
        </CRow>
        <h5>取餐時間</h5>
        <label class="col-form-label">中午</label>
        <div class="d-flex py-3">
          <CButtonGroup>
            <CButton
              v-for="(time, index) in provideTime.noon"
              :key="'time' + index"
              variant="outline"
              color="info"
              :pressed.sync="orderTime.noon[index].active"
            >
              {{ time }}
            </CButton>
          </CButtonGroup>
        </div>
        <label class="col-form-label">晚上</label>
        <div class="d-flex py-3">
          <CButtonGroup>
            <CButton
              v-for="(time, index) in provideTime.night"
              :key="'time' + index"
              variant="outline"
              color="info"
              :pressed.sync="orderTime.night[index].active"
            >
              {{ time }}
            </CButton>
          </CButtonGroup>
        </div>
      </CCardBody>
    </CCard>

    <CCard id="form-item-group" class="my-4">
      <CCardHeader>
        <CAlert :show="mealGroup.length==0" color="danger" class="my-2">請新增商品</CAlert>
        <CRow>
          <CCol>
            <CButton block color="success" @click="addMealGroup('single')"><h3>新增單點</h3></CButton>
          </CCol>
          <CCol>
            <CButton block color="success" @click="addMealGroup('set')"><h3>新增套餐</h3></CButton>
          </CCol>
        </CRow>
      </CCardHeader>
      <CCardBody>
        <CCard body-wrapper v-for="(group, index) in mealGroup" :key="'group' + index">
          <CInput
            v-model="group.title"
            placeholder="分類名稱"
            size="lg"
          >
            <template #prepend>
              <CButton 
                @click="addItem(group)" 
                color="primary"
                class="mb-2"
              >
                新增項目
              </CButton>
            </template>
            <template #append>
              <CButton 
                @click="group.show = !group.show" 
                color="primary"
                class="mb-2"
              >
                {{ group.show ? '開放中' : '關閉中' }}
              </CButton>
            </template>
          </CInput>
          <CCollapse :show="group.show">
            <template v-if="group.items">
              <div :key="'item' + itemIndex" v-for="(item, itemIndex) in group.items">
                <CInput
                  v-model="item.price"
                  placeholder="價錢"
                  size="lg"
                  type="number"
                >
                  <template #prepend>
                    <CSelect
                      :value.sync="item.key"
                      :options="itemList.selectable"
                      size="lg"
                      @update:value="selectItem(item)"
                    />
                  </template>
                  <template #append>
                    <CSwitch class="mx-1" color="primary" :checked.sync="item.show" size="lg" labelOn="顯示" labelOff="隱藏"/>
                    <CSwitch
                      class="mx-1"
                      color="primary"
                      :checked.sync="item.showImg"
                      size="lg"
                      labelOn="有圖"
                      labelOff="無圖"
                      v-if="itemList.rows[item.key] && itemList.rows[item.key].url"
                    />
                  </template>
                </CInput>
              </div>
            </template>
            <template v-if="group.sets">
              <div :key="'set' + setIndex" v-for="(set, setIndex) in group.sets">
                <CRow>
                  <CCol lg="4">
                    <CInput
                      v-model="set.key"
                      description="請自訂id"
                      placeholder="請自訂id"
                      size="lg"
                      prepend="ID"
                    />
                  </CCol>
                  <CCol lg="6">
                    <CInput
                      v-model="set.title"
                      description="請輸入套餐名稱"
                      placeholder="請輸入套餐名稱"
                      size="lg"
                      prepend="名稱"
                    />
                  </CCol>
                  <CCol lg="2">
                    <CSwitch class="mx-1" color="primary" :checked.sync="set.show" size="lg" labelOn="顯示" labelOff="隱藏"/>
                  </CCol>
                </CRow>
                <CRow>
                  <CCol lg="6">
                    <CInput
                      v-model="set.price"
                      description="請輸入套餐價格"
                      placeholder="請輸入套餐價格"
                      size="lg"
                      prepend="價格"
                      type="number"
                    />
                  </CCol>
                  <CCol lg="6">
                    <CInput
                      v-model="set.imgUrl"
                      description="套餐圖片路徑，無則留空"
                      placeholder="套餐圖片路徑.."
                      size="lg"
                      prepend="套餐圖片"
                    />
                  </CCol>
                </CRow>
                <CSelect
                  :value.sync="selectedItem"
                  :options="itemList.selectable"
                  size="lg"
                  @update:value="selectSetItem(set)"
                />
                <CCol>
                  <CCard color="gradient-secondary" class="text-center" body-wrapper>
                    <CAlert
                      :key="'item' + itemIndex"
                      v-for="(itemKey, itemIndex) in set.items"
                      color="secondary"
                      class="alert-dismissible"
                    >
                      {{ itemList.rows[itemKey].name }}
                      <CButton
                        class="position-absolute"
                        color="secondary"
                        style="right:10px;top: 50%;transform: translateY(-50%);"
                        @click="removeItem(set, itemIndex)"
                      >
                        刪除
                      </CButton>
                    </CAlert>
                  </CCard>
                </CCol>
              </div>
            </template>
          </CCollapse>
        </CCard>
      </CCardBody>
    </CCard>

    <CButton @click="sendNewForm" size="lg" color="warning" block class="my-1">
      {{ saveBtnTtile }}
    </CButton>
    
    <Loading :isLoading="isLoading"/>
  </div>
</template>

<script>
import moment from 'moment'
import Loading from '@/views/Loading'

const { getSettingDoc } = require('@/gapi/getSettingDoc');
const { getOrderDoc } = require('@/gapi/getOrderDoc');
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
const provideTime = {
  noon: ['11:30','12:00','12:30','13:00'],
  night: ['17:00','17:30','18:00','18:30','19:00']
}

const defaultTemplate = {
  title: "三葉家庭日,外帶表單",
  describe: `三葉與大家一起防疫大作戰！\n聯絡電話：089-325261\n取餐地點：台東市中山路152號`,
  meal: {
    single: {
      title: '單點',
      show: true,
      items: [{
        key: 0,
        price: 0,
        show: true,
        showImg: true
      }]
    },
    set: {
      title: '小家庭餐',
      show: true,
      sets: [{
        key: null,
        title: '',
        price: 0,
        show: true,
        imgUrl: null,
        items: []
      }]
    }
  }
}

export default {
  name: 'EditForm',
  data () {
    return {
      isLoading: true,
      editingForm: null,
      sheetTitle: {
        valid: false,
        validator: data => {
          return data.valid = data.value ? data.value.length > 0 : false
        },
        value: '0610'
      },
      title: {
        valid: false,
        value: defaultTemplate.title
      },
      describe: {
        valid: false,
        value: defaultTemplate.describe
      },
      validTime: null,
      orderTime: {
        noon: [],
        night: []
      },
      provideTime,
      mealGroup: [],
      selectedItem: null,
      settingDoc: null,
      formListSheet: null,
      formListRows: null,
      itemList: {
        rows: [],
        selectable: [{
          value: 0,
          label: '請選擇品項'
        }]
      },
      checkError: false
    }
  },
  props: {
    editSheet: String,
    pageTitle: String,
    saveBtnTtile: String,
  },
  components:{
    DatePicker,
    Loading
  },
  created() {
    //預設日期資料
    let noon = this.provideTime['noon'];
    let night = this.provideTime['night'];
    for (let index = 0; index < noon.length; index++) {
      const time = noon[index];
      
      this.orderTime.noon.push({time:time, active: true});
    }
    for (let index = 0; index < night.length; index++) {
      const time = night[index];
      
      this.orderTime.night.push({time:time, active: true});
    }
    
    this.accessSpreadSheet();
  },
  methods:{
    async accessSpreadSheet() {
      const adminDoc = await getSettingDoc();
      this.formListSheet = adminDoc.sheetsByTitle['表單列表'];
      this.formListRows = await this.formListSheet.getRows();
      const itemListSheet = adminDoc.sheetsByTitle['品項'];
      const itemListRows = await itemListSheet.getRows();
      //編輯表格
      for (let index = 0; index < this.formListRows.length; index++) {
        const form = this.formListRows[index];
        //編輯表單
        if (form.sheetTitle == this.editSheet) {
          this.editingForm = form;
          this.sheetTitle.value = form.sheetTitle;
          this.title.value = form.title;
          this.describe.value = form.describe;
          this.validTime = [moment(form.start_time).format("YYYY-MM-DD HH:mm"), moment(form.end_time).format("YYYY-MM-DD HH:mm")];
          this.describe.value = form.describe;
          this.mealGroup = JSON.parse(form.groups);
          const _order_time = JSON.parse(form.order_time);
          for (let timeIndex = 0; timeIndex < this.orderTime.noon.length; timeIndex++) {
            const item = this.orderTime.noon[timeIndex];
            
            this.orderTime.noon[timeIndex].active = false;
            if (_order_time.includes(item.time)) {
              this.orderTime.noon[timeIndex].active = true;
            }
          }
          for (let timeIndex = 0; timeIndex < this.orderTime.night.length; timeIndex++) {
            const item = this.orderTime.night[timeIndex];
            
            this.orderTime.night[timeIndex].active = false;
            if (_order_time.includes(item.time)) {
              this.orderTime.night[timeIndex].active = true;
            }
          }

          break;
        }
      }

      for (let index = 0; index < itemListRows.length; index++) {
        const item = itemListRows[index];
        //品項
        if (item.id) {
          this.itemList.selectable.push({
            value: item.id,
            label: item.name
          });
          this.itemList.rows[item.id] = item;
        }
      }

      this.isLoading = false;
    },
    disabledBeforeTodayAndAfterAWeek(date) {
      const today = new Date();
      today.setHours(0, 0, 0, 0);
      return date < today;
    },
    addMealGroup: function(type) {
      this.mealGroup.push(defaultTemplate.meal[type]);
    },
    addItem: function(group) {
      let lastItem = group.items[group.items.length - 1];
      
      if ( lastItem.key != null && lastItem.key != 0 ) {
        group.items.push({
          key: 0,
          price: 0,
          show: true,
          showImg: true
        });
      }
    },
    //選擇單點 預設價格
    selectItem: function(item) { 
      item.price = this.itemList.rows[item.key].price;
    },
    //選擇套餐內容
    selectSetItem: function(set) { 
      if (this.selectedItem != 0) {
        set.items.push(this.selectedItem);
      }
    },
    //刪除套餐內容
    removeItem: function(set, itemIndex) { 
      set.items.splice(itemIndex, 1);
    },
    async sendNewForm(){
      this.checkError = false;
      let _app = this;
      
      if (this.editSheet == null) {
        for (let index = 0; index < this.formListRows.length; index++) {
          const formList = this.formListRows[index];
          
          if (formList.sheetTitle == this.sheetTitle.value) {
            alert("重複的試算表名稱");
            _app.$scrollTo("#form-info");
            this.checkError = true;
            break;
          }
        }
      }
      
      //菜單內容
      for (let index = 0; index < this.mealGroup.length; index++) {
        const group = this.mealGroup[index];
        
        if (group.items) {
          for (let i = 0; i < group.items.length; i++) {
            const item = group.items[i];
            
            if (item.show && item.key == 0) {
              alert("請選擇商品");
              this.checkError = true;
              break;
            }
          }
        }
        if (group.sets) {
          for (let i = 0; i < group.sets.length; i++) {
            const set = group.sets[i];
            
            if (set.show) {
              if (set.title == '') {
                alert("請填入套餐名稱");
                this.checkError = true;
                break;
              }
              if (set.items.length == 0) {
                alert("請選擇套餐內容");
                this.checkError = true;
                break;
              }
            }
          }
        }
      }

      if (!this.checkError) {
        if (!this.sheetTitle.valid) {
          this.$scrollTo("#form-info");
          return;
        }
        if (!this.title.valid) {
          this.$scrollTo("#form-info");
          return;
        }
        if (!this.describe.valid) {
          this.$scrollTo("#form-info");
          return;
        }
        if (this.validTime == null) {
          this.$scrollTo("#form-valid-time");
          return;
        }
        if (this.mealGroup.length == 0) {
          this.$scrollTo("#form-item-group");
          return;
        }
        //取餐時間
        let timelist = [];
        for (let index = 0; index < this.orderTime['noon'].length; index++) {
          const time = this.orderTime['noon'][index];
          
          if (time.active) timelist.push(time.time);
        }
        for (let index = 0; index < this.orderTime['night'].length; index++) {
          const time = this.orderTime['night'][index];
          
          if (time.active) timelist.push(time.time);
        }
        //修改表單
        if (this.editSheet) {
          this.editingForm.title = this.title.value;
          this.editingForm.describe = this.describe.value;
          this.editingForm.start_time = moment(this.validTime[0]).format("YYYY-MM-DD HH:mm");
          this.editingForm.end_time = moment(this.validTime[1]).format("YYYY-MM-DD HH:mm");
          this.editingForm.order_time = JSON.stringify(timelist);
          this.editingForm.groups = JSON.stringify(this.mealGroup);

          this.editingForm.save({
            raw: true
          });
        } else {
          //新增表單設定
          this.formListSheet.addRow({
            sheetTitle: "'" + this.sheetTitle.value,
            title: this.title.value,
            describe: this.describe.value,
            start_time: "'" + moment(this.validTime[0]).format("YYYY-MM-DD HH:mm"),
            end_time: "'" + moment(this.validTime[1]).format("YYYY-MM-DD HH:mm"),
            order_time: JSON.stringify(timelist),
            groups: JSON.stringify(this.mealGroup),
          }, {
            raw: true
          });
          //新增表單
          const doc = await getOrderDoc();
          await doc.addSheet({
            title: this.sheetTitle.value,
            headerValues: ["timestamp","ip","name","phone","take_time","order_time","note","detail","count","sum","justifyPrice"]
          });
        }

        alert("OK!");
      }
    }
  }
}
</script>